* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
  background-color: #f5f5f5;
}
nav {
  background-color: green;
  text-align: center;
  padding: 1rem;
  margin-bottom: 1rem;
}
button {
  width: 150px;
}
.commandBtns button {
  padding: 0.7rem 1rem;
}
.top-commandBtns button {
  background-color: hotpink;
}
.side-commandBtns {
  max-width: 200px;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 1rem;
}
.side-commandBtns button {
  background-color: teal;
}
.side-commandBtns .run-btn {
  padding: 2rem;
  background-color: greenyellow;
  font-weight: 900;
  font-size: 2rem;
}
